.technologies__container {
    display: flex;
    justify-content: center;
    margin-bottom: 100px;
}

.technologies__wrapper {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 10px;


    @media screen and (min-width: 768px) {
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 30px;
    }
}

.technologies__item {
    padding: 10px 0;
    display: flex;
    align-items: center;
    justify-content: start;
    color: #fff;
    background-color: #1d2430;

}

.technologies__icon {
    margin-left: 10px;
    margin-right: 10px;
    color: #30cc8e;
    width: 30px;
}