.show__menu {
    width: 300px;
    height: 500px;
    position: fixed;
    z-index: 20;
    top: 0;
    right: 0;
    opacity: 0.85;
    background-color: #161c24;
    transition: all 1s;
}

.hide__menu {
    display: none;
}

.burger__nav {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    list-style: none;
}

.nav__item {
        margin-bottom: 10px;
}

.nav__link {
    text-decoration: none;
    font-size: 30px;
    color: #878e9b;
}